  * {
    box-sizing: border-box;
    }

  .card {
    width: 200px;
    padding: 32px 16px 16px;
    border-radius: 5px;
    border: 1px solid $milk-back;
    background: $white-main;

    &:hover {
      transform: scale(120%);
    }
    
    &__img {
      display: flex;
      width: 160px;
      height: 134px;

      margin: 0 auto 40px;
    }

    &__title {
      margin-bottom: 4px;
      color: $text-color-main;
      font-size: 12px;
      font-weight: 500;
      line-height: 18px;
    }

    &__product_code {
      color: $text-color-secondary;
      font-size: 10px;
      line-height: 14px;

      margin-bottom: 16px;
    }

    &__rating {
      display: flex;
      align-items: center;
      justify-content: space-between;

      margin-bottom: 24px;
    }

    &__reviews {
      color: $text-color-secondary;
    }

    &__prise {
      display: flex;
      align-items: center;
      justify-content: space-between;

      margin-bottom: 16px;

      &_text {
        color: $text-color-secondary;
        font-size: 12px;
        line-height: 18px;
      }

      &_number {
        font-size: 16px;
        line-height: 18px;
        font-weight: bold;
        color: $text-color-main;
      }
    }

    &__buy {
      padding-top: 12px;
      padding-bottom: 12px;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: $blue-accent;
      color: $white-main;
      border: 1px solid $blue-accent;
      border-radius: 5px;
      cursor: pointer;
      font-size: 14px;
      font-weight: 700;
      text-transform: uppercase;
      transition: background-color 0.3s, border 0.3s, color 0.3s;
      text-decoration: none;

    &:hover {
      background-color: $white-main;
      color: $blue-accent;
    }
  }
  }
